export default {
	data() {
		return {
			iswait:1,
			datalist:[]
		}
	},
	onLoad() {
		uni.getStorage({
			key:'userinfo',
			success:(res)=>{
				this.getdata(res.data.id)
			}
		})
	},
	methods: {
		getdata(user_id){
			uni.request({
				url:this.common.websiteUrl+"experhome_owners_index",
				header:{"user-token":"6a109faf305513d443337ddb1ad4cb9b"},
				method:"post",
				data:{'user_id':user_id},
				success: (res) => {
					this.datalist = res.data.data;
				}
			});
		},
		settime(){
			uni.navigateTo({
			    url: '../settime/settime'
			});
		},
		waitexamine(){
			uni.navigateTo({
			    url: '../waitexamine/waitexamine'
			});
		},
		waitvisit(){
			uni.navigateTo({
			    url: '../waitvisit/waitvisit'
			});
		},
		finishvisit(){
			uni.navigateTo({
			    url: '../finishvisit/finishvisit'
			});
		},
		back(){
			uni.switchTab({
			    url: '/pages/my/my'
			});
		}
	}
}
